
.button {
    height: 2em;
    margin-bottom: 1em;
    background: #838383;
    border-radius: 5px;
    border: 0;
    color: white;
    width: 8em;
    margin-left: 0.5em;
    cursor: pointer;
}

.container {
    max-width: 1140px;
    display: flex;
    justify-content: flex-end;
    margin: 1em auto 0;
}